import React, { Component } from "react";
import { getQuestions } from "./services/questionnaire";

class Questions extends Component {
  state = {
    questions: getQuestions(),
    currentQuestion: 0,
    answered: "",
    errors: 0,
  };

  handleChange = (e) => {
    this.setState({ answered: e.target.value });
  };

  handleSubmit = (e) => {
    const { answered } = this.state;
    e.preventDefault();
    this.handleEvaluate(answered);
  };

  handleEvaluate = (answered) => {
    const { currentQuestion, questions, errors } = this.state;
    questions[currentQuestion].correctAnswers.includes(answered)
      ? this.setState({ currentQuestion: currentQuestion + 1, answered: "" })
      : this.setState({ errors: errors + 1, answered: "" });
  };

  render() {
    const { questions, currentQuestion, answered, errors } = this.state;
    let isEnabled = true;

    return (
      <section className="full-bg">
        <div className="card">
          <h3 className="card__title">Recite your baseline</h3>
          <div className="card__question">
            {errors < 5
              ? currentQuestion < questions.length
                ? ((isEnabled = true),
                  (
                    <>
                      <h4 className="card__question-primary">
                        {questions[currentQuestion].question}
                      </h4>
                      <form className="form__group">
                        <input
                          id="mainInput"
                          disabled={!isEnabled}
                          onChange={this.handleChange}
                          placeholder="Your answer"
                          value={answered}
                          type="text"
                        />
                        <button
                          className="form__button noselect"
                          disabled={!isEnabled}
                          onClick={this.handleSubmit}
                        >
                          Answer
                        </button>
                      </form>
                    </>
                  ))
                : ((isEnabled = false),
                  (
                    <>
                      <h4 className="card__question-primary">
                        A blood black nothingness. A system of cells. Within
                        cells interlinked. Within one stem. And dreadfully
                        distinct. Against the dark. A tall white fountain
                        played.
                      </h4>
                      <h4 className="card__question-congrats">
                        Congratulations! We're done.
                      </h4>
                    </>
                  ))
              : ((isEnabled = false),
                (
                  <h4 className="card__question-danger">
                    ...You're not even close to Baseline.
                  </h4>
                ))}
          </div>
        </div>
      </section>
    );
  }
}

export default Questions;
